<template>
  <div id="container">
    <HeaderUserBar />

    <div id="my-tickets">
      <div class="mt-title">Мои тикеты</div>
      <div class="mt-list" v-if="tickets_total">
        <div class="ticket-id " v-for="ticket in tickets_data"><span :class="ticket.status">№ {{ ticket.code }}</span></div>
      </div>
      <div class="mt-no-ticket" v-else>Тикетоа пока нет!</div>
    </div>
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";
export default {
  name: "MyTickets",
  data: () => ({
    tickets_data: 0,
    tickets_total: 0,
  }),
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  async mounted() {
    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => this.$router.push('/'));

    await axios.get('https://server.gold-ticket.uz/api/ticket/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          if (response.data.data) {
            this.tickets_data = response.data.data
            this.tickets_total = response.data.total
          }
        })
        .catch(error => console.log(error));

  }
}
</script>
